.header {
  display: flex;
  justify-content:space-between;
  border: 1px solid black;
}

.logo {
  margin: 5px;
  width: 100px;
}

.nav-items {
  font-size: 24px;
  padding: 20px 20px;
}

.nav-items > ul {
  display: flex;

  list-style-type: none;
}

.nav-items > ul > li {
  padding: 10px;
  margin: 10px;
}

.res-card {
  margin:5px;
  padding: 10px 20px;
  width: 200px;
  height: 300px;
}


.res-container {
    display: flex;
    flex-wrap: wrap;
}
.res-card:hover {
    cursor: pointer;
    border: 1px solid black;
}

.search {
    padding: 10px;
}

.res-logo {
    margin: 5px;
    width: 100%;
}
